const initialState = {
    navData: [
        {id: 1, link: "/users", text: ""},
        {id: 2, link: "/messages", text: ""},
        {id: 3, link: "/news", text: ""},
        {id: 4, link: "/music", text: ""},
        {id: 5, link: "/settings", text: ""}
    ]
};

const navbarReducer = (state=initialState, action) => {
    switch (action.type) {
        default:
            return state;
    }
};

export default navbarReducer;
